* {
  box-sizing: border-box;
}

body {
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
  color: #212121;
  background-color: #fafafa;
  font-family: -apple-system, BlinkMacSystemFont, Segoe UI, Roboto, Oxygen-Sans, Ubuntu, Cantarell, Helvetica Neue, sans-serif;
  line-height: 1.5;
}

img {
  max-width: 100%;
  display: block;
}

.js-form {
  border: 1px solid #000;
  border-radius: 4px;
  margin-left: 200px;
  padding: 20px;
  display: inline-block;
}

input {
  margin: 20px;
  display: block;
}

label, .practice__ref {
  display: block;
}

textarea {
  resize: none;
}

.from-set-time {
  background-color: #00f;
  border: 1px solid #000;
  align-items: center;
  margin-top: 40px;
  display: flex;
  position: relative;
}

.baton {
  height: 22px;
}

.oject {
  opacity: 0;
  width: 80px;
  height: 80px;
  background-color: orange;
  border: 2px solid #000;
  border-radius: 50%;
  transition: opacity .25s linear;
  position: absolute;
  top: -10px;
  left: 300px;
}

.oject.active {
  opacity: 1;
}

